  }
}

[data-aos="new"] {
  opacity: 0;
  transition-property: transform, opacity;
}
[data-aos="new"].aos-animate {
  opacity: 1;
}
@media screen and (min-width: 768px) {
  [data-aos="new"] {
    transform: translateY(30px);
  }
  [data-aos="new"].aos-animate {
    transform: translateY(0);
  }
}

